import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdjustmentComponent } from './adjustment/adjustment.component';
import { CropPhotoComponent } from './crop-photo/crop-photo.component';
import { HomeComponent } from './home/home.component';
import { SelectPhotoComponent } from './select-photo/select-photo.component';
import { UploadPhotoComponent } from './upload-photo/upload-photo.component';


const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'home', component: HomeComponent },
  { path: 'upload-photo', component: UploadPhotoComponent },
  { path: 'crop-photo', component: CropPhotoComponent },
  { path: 'adjustment', component: AdjustmentComponent },
  { path: 'select-photo', component: SelectPhotoComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
