import { Component, Output, EventEmitter } from '@angular/core';
import { HttpEventType, HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-upload-photo',
  templateUrl: './upload-photo.component.html',
  styleUrls: ['./upload-photo.component.css'],
})
export class UploadPhotoComponent {
  public waiting_panel: boolean = false;
  public browse_panel: boolean = true;
  @Output() public onUploadFinished = new EventEmitter();

  constructor(private http: HttpClient, private router: Router) {}

  public uploadFile = (files) => {
    if (files.length === 0) {
      return;
    }
    let fileToUpload = <File>files[0];
    const formData = new FormData();
    formData.append('file', fileToUpload, fileToUpload.name);
    this.http
      .post( 'https://' +'cors-anywhere.herokuapp.com/' + 'http://nutanusadadiya-001-site1.dtempurl.com/api/upload', formData, {
        reportProgress: true,
        observe: 'events',
      })
      .subscribe((event) => {
        if (event.type === HttpEventType.UploadProgress) {
          this.browse_panel = false;
          this.waiting_panel = true;
        } else if (event.type === HttpEventType.Response) {
          localStorage.setItem(
            'croppedImage',
            'https://' +'cors-anywhere.herokuapp.com/' + 'http://nutanusadadiya-001-site1.dtempurl.com/resources/images/' + fileToUpload.name
          );
          this.router.navigate(['/crop-photo']);
        }
      });
  };
}
